
var arrayLike = require('./array-like');

module.exports = function assure(obj, from, names) {
  var i = arrayLike(names) ? 0 : 2;
  var args = i === 0 ? names : arguments;
  for (var n=args.length; i<n; i++) {
    var name = args[i];
    var val = from[name];
    if (val == null) throw Error('missing ' + name);
    obj[name] = val;
  }
  return obj;
};

