
const forOwn = require('./for-own');

function preorder(obj, fn, depth, key, host) {
  fn(obj, key, host);

  if (depth > 0) {
    forOwn(obj, (val, key) => preorder(val, fn, depth-1, key, obj));
  }
}
module.exports = function(obj, fn, depth) {
  depth = depth >= 0 ? depth : 10;
  preorder(obj, fn, depth);
};

