'use strict';

function define(obj, prop, target, mthd, args) {
  if (typeof target === 'string') {
    args = mthd;
    mthd = target;
    target = obj;
  }
  args = args || [];
  var fn = target[mthd];
  var ret;
  Object.defineProperty(obj, prop, {
    get: function() { return ret === undefined ? (ret = fn.apply(target, args)) : ret; }
  });
  return obj;
}

define.forward = function delegate(obj, fields, target, renames) {
  fields.forEach(function(field) {
    var name = renames && renames[field] || field; 
    Object.defineProperty(obj, field, {
      get: function() { return target[name]; }
    });
  });
  return obj;
};

module.exports = define;
