'use strict';

var ui = Object.assign({
  assetLoader: require('./asset-loader'),
  eventSource: require('./event-source'),
  Comp: require('./comp'),
  Cards: require('./cards'),
  rd: function() {
    var n = arguments.length, fn = arguments[n-1];
    ui.rdall([].slice.call(arguments, 0, n-1)).then(function(args) {
      fn.apply(null, args);
    });
  },
}, require('./loadjs'), require('./dom'));

module.exports = ui;
