'use strict';

var arrayLike = require('./array-like');

module.exports = function each(obj, fn, me) {
  if (!obj || typeof obj !== 'object') return;

  if (arrayLike(obj)) {
    for (var i=0, n=obj.length; i<n; i++) {
      fn.call(me, obj[i], i);
    }
  } else {
    Object.keys(obj).forEach(function(key) {
      fn.call(me, obj[key], key);
    });
  }
};
